import React, { useState, useEffect } from 'react';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import { settingsApi } from '../api/settingsApi';
import Spinner from '../components/common/Spinner';
import ErrorMessage from '../components/common/ErrorMessage';

// 
interface CompanySettings {
  company_name: string;
  company_code: string;
  company_address: string;
  legal_rate: number;
  fiscal_year_start: string;
  fiscal_year_end: string;
  monthly_report_reminder: boolean;
  legal_rate_alert: boolean;
  employment_end_notice: boolean;
  theme: string;
  language?: string;
  notifications?: boolean;
}

// CompanySettings
interface ExtendedCompanySettings extends CompanySettings {
  language?: string;
  notifications?: boolean;
}
interface User {
  id: number;
  username: string;
  role: string;
  created_at?: string;
}

const Settings: React.FC = () => {
  const [activeTab, setActiveTab] = useState<string>('application');
  const queryClient = useQueryClient();
  
  // React Query: 
  const { 
    data: companySettings, 
    isLoading: isLoadingSettings, 
    error: settingsError 
  } = useQuery(
    'companySettings',
    settingsApi.getCompanySettings,
    {
      // 
      onError: () => {
        return {
          company_name: '',
          company_code: '',
          company_address: '',
          legal_rate: 2.3,
          fiscal_year_start: '',
          fiscal_year_end: '',
          monthly_report_reminder: true,
          legal_rate_alert: true,
          employment_end_notice: false,
          theme: 'light',
          language: 'ja',
          notifications: true
        };
      }
    }
  );
  
  // React Query: account
  const { 
    data: users, 
    isLoading: isLoadingUsers, 
    error: usersError 
  } = useQuery(
    'users',
    settingsApi.getUsers,
    { enabled: activeTab === 'account' } // account
  );
  
  // 
  const [formData, setFormData] = useState<CompanySettings>({
    company_name: '',
    company_code: '',
    company_address: '',
    legal_rate: 2.3,
    fiscal_year_start: '',
    fiscal_year_end: '',
    monthly_report_reminder: true,
    legal_rate_alert: true,
    employment_end_notice: false,
    theme: 'light',
    language: 'ja',
    notifications: true
  });
  
  const [passwordData, setPasswordData] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });
  
  // 
  useEffect(() => {
    if (companySettings) {
      // companySettingsExtendedCompanySettings
      const extendedSettings = companySettings as ExtendedCompanySettings;
      setFormData({
        ...companySettings,
        language: extendedSettings.language || 'ja',
        notifications: extendedSettings.notifications !== undefined 
          ? extendedSettings.notifications 
          : true
      });
    }
  }, [companySettings]);
  
  // Mutation: 
  const updateSettingsMutation = useMutation(
    (newSettings: CompanySettings) => settingsApi.updateCompanySettings(newSettings),
    {
      onSuccess: () => {
        queryClient.invalidateQueries('companySettings');
        alert('');
      },
      onError: () => {
        alert('');
      }
    }
  );
  
  // Mutation: 
  const changePasswordMutation = useMutation(
    (passwordData: any) => settingsApi.changePassword(passwordData),
    {
      onSuccess: () => {
        alert('');
        setPasswordData({
          currentPassword: '',
          newPassword: '',
          confirmPassword: ''
        });
      },
      onError: () => {
        alert('');
      }
    }
  );
  
  // 
  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>
  ) => {
    const { name, value, type } = e.target;
    
    // 
    if (type === 'checkbox') {
      const checked = (e.target as HTMLInputElement).checked;
      setFormData(prev => ({
        ...prev,
        [name]: checked
      }));
      return;
    }
    
    // 
    if (name === 'legal_rate') {
      setFormData(prev => ({
        ...prev,
        [name]: parseFloat(value) || 0
      }));
      return;
    }
    
    // 
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };
  
  // 
  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setPasswordData(prev => ({
      ...prev,
      [name]: value
    }));
  };
  
  // 
  const handleAppSettingsSave = () => {
    // 
    const appSettings = {
      ...companySettings,
      theme: formData.theme,
      language: formData.language,
      notifications: formData.notifications
    };
    
    updateSettingsMutation.mutate(appSettings as CompanySettings);
  };
  
  // 
  const handleLegalRateUpdate = () => {
    // 
    const legalRateSettings = {
      ...companySettings,
      legal_rate: formData.legal_rate
    };
    
    updateSettingsMutation.mutate(legalRateSettings as CompanySettings);
  };
  
  // 
  const handlePasswordSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    // 
    if (passwordData.newPassword !== passwordData.confirmPassword) {
      alert('');
      return;
    }
    
    if (passwordData.newPassword.length < 8) {
      alert('8');
      return;
    }
    
    changePasswordMutation.mutate(passwordData);
  };
  
  // 
  const handleCompanyFormSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    updateSettingsMutation.mutate(formData);
  };
  
  // 
  const handleThemeChange = (newTheme: string) => {
    setFormData(prev => ({
      ...prev,
      theme: newTheme
    }));
    
    // 
    document.documentElement.setAttribute('data-theme', newTheme);
  };
  
  // 
  if (isLoadingSettings && activeTab !== 'account') {
    return <Spinner />;
  }
  
  // 
  if (settingsError && activeTab !== 'account') {
    return <ErrorMessage message="" />;
  }
  
  const tabItems = [
    { id: 'application', label: '' },
    { id: 'account', label: '' },
    { id: 'company', label: '' },
    { id: 'legal', label: '' },
    { id: 'notifications', label: '' }
  ];

  return (
    <div className="page-container">
      <h1 className="page-title"></h1>
      
      <div className="tab-container" style={{ marginBottom: '20px' }}>
        <div className="tabs">
          {tabItems.map(tab => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`tab-button ${activeTab === tab.id ? 'active' : ''}`}
            >
              {tab.label}
            </button>
          ))}
        </div>
      </div>
      
      {/*  */}
      {activeTab === 'application' && (
        <div className="chart-container">
          <h2 style={{ fontSize: '16px', marginBottom: '15px' }}></h2>
          
          <div style={{ marginBottom: '20px' }}>
            <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
            <div style={{ display: 'flex', gap: '10px' }}>
              <button 
                className={formData.theme === 'light' ? 'active' : 'secondary'} 
                onClick={() => handleThemeChange('light')}
              >
                
              </button>
              <button 
                className={formData.theme === 'dark' ? 'active' : 'secondary'} 
                onClick={() => handleThemeChange('dark')}
              >
                
              </button>
            </div>
          </div>
          
          <div style={{ marginBottom: '20px' }}>
            <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
            <select 
              name="language"
              value={formData.language}
              onChange={handleChange}
              style={{ width: '200px' }}
            >
              <option value="ja"></option>
              <option value="en">English</option>
            </select>
          </div>
          
          <div style={{ marginBottom: '20px' }}>
            <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
            <label style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }}>
              <input 
                type="checkbox" 
                name="notifications"
                checked={formData.notifications} 
                onChange={handleChange}
                style={{ marginRight: '10px' }}
              />
              
            </label>
          </div>
          
          <div style={{ marginTop: '20px' }}>
            <button 
              onClick={handleAppSettingsSave}
              disabled={updateSettingsMutation.isLoading}
            >
              {updateSettingsMutation.isLoading ? '...' : ''}
            </button>
          </div>
        </div>
      )}
      
      {/*  */}
      {activeTab === 'account' && (
        <div className="chart-container">
          <h2 style={{ fontSize: '16px', marginBottom: '15px' }}></h2>
          
          <div style={{ marginBottom: '20px' }}>
            <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
            <form onSubmit={handlePasswordSubmit}>
              <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', maxWidth: '400px' }}>
                <input 
                  type="password" 
                  name="currentPassword"
                  value={passwordData.currentPassword}
                  onChange={handlePasswordChange}
                  placeholder="" 
                  required
                />
                <input 
                  type="password" 
                  name="newPassword"
                  value={passwordData.newPassword}
                  onChange={handlePasswordChange}
                  placeholder="" 
                  required
                />
                <input 
                  type="password" 
                  name="confirmPassword"
                  value={passwordData.confirmPassword}
                  onChange={handlePasswordChange}
                  placeholder=" ()" 
                  required
                />
              </div>
              <button 
                type="submit" 
                style={{ marginTop: '10px' }}
                disabled={changePasswordMutation.isLoading}
              >
                {changePasswordMutation.isLoading ? '...' : ''}
              </button>
            </form>
          </div>
          
          {/*  () */}
          {users && users.length > 0 && (
            <div style={{ marginTop: '30px' }}>
              <h3 style={{ fontSize: '16px', marginBottom: '15px' }}></h3>
              
              {isLoadingUsers ? (
                <Spinner />
              ) : usersError ? (
                <ErrorMessage message="" />
              ) : (
                <>
                  <div style={{ marginBottom: '20px' }}>
                    <button className="btn-primary"></button>
                  </div>
                  
                  <table className="table">
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {users.map((user: any) => (
                        <tr key={user.id}>
                          <td>{user.id}</td>
                          <td>{user.username}</td>
                          <td>{user.role === 'admin' ? '' : ''}</td>
                          <td>{user.created_at ? new Date(user.created_at).toLocaleDateString('ja-JP') : '-'}</td>
                          <td>
                            <button className="btn-secondary btn-sm" style={{ marginRight: '5px' }}></button>
                            <button className="btn-danger btn-sm"></button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </>
              )}
            </div>
          )}
        </div>
      )}
      
      {/*  */}
      {activeTab === 'company' && (
        <div className="chart-container">
          <h2 style={{ fontSize: '16px', marginBottom: '15px' }}></h2>
          
          <form onSubmit={handleCompanyFormSubmit}>
            <div style={{ marginBottom: '20px' }}>
              <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
              <input 
                type="text" 
                name="company_name"
                value={formData.company_name}
                onChange={handleChange}
                style={{ width: '100%', maxWidth: '400px' }}
              />
            </div>
            
            <div style={{ marginBottom: '20px' }}>
              <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
              <input 
                type="text" 
                name="company_code"
                value={formData.company_code}
                onChange={handleChange}
                style={{ width: '100%', maxWidth: '400px' }}
              />
            </div>
            
            <div style={{ marginBottom: '20px' }}>
              <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
              <input 
                type="text" 
                name="company_address"
                value={formData.company_address}
                onChange={handleChange}
                style={{ width: '100%', maxWidth: '400px' }}
              />
            </div>
            
            <div style={{ marginBottom: '20px' }}>
              <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
              <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                <input 
                  type="date" 
                  name="fiscal_year_start"
                  value={formData.fiscal_year_start}
                  onChange={handleChange}
                />
                <span></span>
                <input 
                  type="date" 
                  name="fiscal_year_end"
                  value={formData.fiscal_year_end}
                  onChange={handleChange}
                />
              </div>
            </div>
            
            <div style={{ marginTop: '20px' }}>
              <button 
                type="submit"
                disabled={updateSettingsMutation.isLoading}
              >
                {updateSettingsMutation.isLoading ? '...' : ''}
              </button>
            </div>
          </form>
        </div>
      )}
      
      {/*  */}
      {activeTab === 'legal' && (
        <div className="chart-container">
          <h2 style={{ fontSize: '16px', marginBottom: '15px' }}></h2>
          
          <div style={{ marginBottom: '20px' }}>
            <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
            <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
              <input 
                type="text" 
                name="legal_rate"
                value={formData.legal_rate} 
                onChange={handleChange}
                style={{ width: '100px' }}
              />
              <span>%</span>
            </div>
            <div style={{ marginTop: '10px', fontSize: '12px', color: '#6c757d' }}>
               
            </div>
            <button 
              style={{ marginTop: '10px' }}
              onClick={handleLegalRateUpdate}
              disabled={updateSettingsMutation.isLoading}
            >
              {updateSettingsMutation.isLoading ? '...' : ''}
            </button>
          </div>
        </div>
      )}
      
      {/*  */}
      {activeTab === 'notifications' && (
        <div className="chart-container">
          <h2 style={{ fontSize: '16px', marginBottom: '15px' }}></h2>
          
          <div style={{ marginBottom: '20px' }}>
            <div style={{ fontWeight: 'bold', marginBottom: '10px' }}></div>
            
            <div style={{ marginBottom: '10px' }}>
              <label style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }}>
                <input 
                  type="checkbox" 
                  name="monthly_report_reminder"
                  checked={formData.monthly_report_reminder} 
                  onChange={handleChange}
                  style={{ marginRight: '10px' }}
                />
                
              </label>
            </div>
            
            <div style={{ marginBottom: '10px' }}>
              <label style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }}>
                <input 
                  type="checkbox" 
                  name="legal_rate_alert"
                  checked={formData.legal_rate_alert} 
                  onChange={handleChange}
                  style={{ marginRight: '10px' }}
                />
                
              </label>
            </div>
            
            <div style={{ marginBottom: '10px' }}>
              <label style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }}>
                <input 
                  type="checkbox" 
                  name="employment_end_notice"
                  checked={formData.employment_end_notice} 
                  onChange={handleChange}
                  style={{ marginRight: '10px' }}
                />
                30
              </label>
            </div>
            
            <div style={{ marginTop: '20px' }}>
              <button 
                onClick={handleCompanyFormSubmit}
                disabled={updateSettingsMutation.isLoading}
              >
                {updateSettingsMutation.isLoading ? '...' : ''}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Settings;

