// App.tsx
import React from 'react';
import {
   BrowserRouter as Router,
   Routes,
   Route,
   Navigate
} from 'react-router-dom';

import { QueryClient, QueryClientProvider } from 'react-query';
import Layout from './components/Layout';
import Dashboard from './pages/Dashboard';
import EmployeeList from './pages/EmployeeList';
import EmployeeDetailPage from './pages/EmployeeDetailPage';
// 
import MonthlyReport from './pages/MonthlyReport/index';
import MonthlyReportDetail from './pages/MonthlyReport/MonthlyReportDetail';
import PaymentReport from './pages/PaymentReport';
import Settings from './pages/Settings';
import './App.css';

// 
window.process = window.process || {};
window.process.env = window.process.env || {};
window.process.env.REACT_APP_USE_MOCK = 'false'; // API


// QueryClient
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      retry: false,
      refetchOnWindowFocus: false,
      staleTime: 30000,
    },
  },
});

// 
const App: React.FC = () => {
  // 
  console.log('App.tsx loaded');
  
  return (
    <QueryClientProvider client={queryClient}>
      <Router>
        <Routes>
          <Route path="/" element={<Layout />}>
            <Route index element={<Dashboard />} />
            <Route path="/dashboard" element={<Dashboard />} />
            <Route path="/employee-list" element={<EmployeeList />} />
            
            {/*  */}
            <Route path="/employees/new" element={<div></div>} />
            <Route path="/employee-detail/:id" element={<EmployeeDetailPage />} />
            <Route path="/employee-edit/:id" element={<div></div>} />
            
            {/*  */}
            <Route path="/employees/:id" element={<EmployeeDetailPage />} />
            <Route path="/employees/:id/edit" element={<div></div>} />
            
            {/*  -  */}
            <Route path="/monthly-report/detail" element={<MonthlyReportDetail />} />
            <Route path="/monthly-report/:id" element={<MonthlyReportDetail />} />
            <Route path="/monthly-report" element={<MonthlyReport />} />
            
            <Route path="/payment-report" element={<PaymentReport />} />
            <Route path="/settings" element={<Settings />} />
            <Route path="*" element={<Navigate to="/dashboard" />} />
          </Route>
        </Routes>
      </Router>
    </QueryClientProvider>
  );
};

export default App;